<style scoped>
  #resource {
    width: 100%;
    height: 100%;
    background-color: #eeeeee;
  }

  #resourceTree {
    border: 1px solid #c1c1c1;
    height: 98%;
    width: 12%;
    border-radius: 10px;
    overflow: hidden;
  }

  .title {
    height: 40px;
    background-color: #d33a31;
    line-height: 40px;
    padding-left: 10px;
    font-size: 1.4rem;
    color: white;
    letter-spacing: 3px;
  }

  .controlTree {
    height: 40px;
    line-height: 40px;
    text-align: right;
    padding-right: 10px;
    border-bottom: 1px solid #dedede;
  }

  .controlTree > a {
    margin-left: 15px;
  }

  .controlTree > a > i {
    margin-right: 3px;
  }

  .controlTree > a:hover {
    color: #d33a31;
  }

  #resourceList {
    border: 1px solid #c1c1c1;
    height: 98%;
    border-radius: 10px;
    overflow: hidden;
    width: 87%;
  }

  .controlBox {
    height: 40px;
    line-height: 40px;
    text-align: right;
    padding: 10px;
    border-bottom: 1px solid #e0e0e0;
    display: flex;
    justify-content: space-between;
  }

  .control a {
    margin-left: 10px;
    font-size: 1.4rem;
    border-right: 1px solid #cfcfcf;
    padding-right: 10px;
  }

  .control > a > i {
    margin-right: 5px;
  }

  .search {
    display: flex;
  }

  .search > div {
    margin-right: 10px;
  }

  .imgList, .tableList {
    padding: 20px;
    height: 580px;
  }

  .resourceList {
    display: flex;
    flex-wrap: wrap;
  }

  .resourceList li {
    width: 150px;
    height: 150px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 30px 40px;
    cursor: pointer;
    padding: 10px 5px;
    border-radius: 5px;
    position: relative;
  }

  .imgBox {
    width: 130px;
    height: 130px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    border: 1px solid #e7e7e7;
  }

  .resourceList li img {
    max-width: 120px;
    max-height: 120px;
  }

  .resourceList li:hover {
    background-color: #ebebeb !important;
  }

  .resourceList p {
    min-width: 20px;
  }

  .el-upload-list__item-status-label {
    position: absolute;
    right: -15px;
    top: -7px;
    width: 46px;
    height: 26px;
    background: #13ce66;
    text-align: center;
    transform: rotate(45deg);
    box-shadow: 0 1px 1px #ccc;
  }

  .el-upload-list__item-status-label i {
    font-size: 12px;
    margin-top: 12px;
    transform: rotate(-45deg);
    color: white;
  }

  .page {
    text-align: right;
    padding-right: 20px;
  }
</style>

<template>
  <div id="resource">
    <nav-bar @lang-change="langChange"></nav-bar>
    <breadcrumb></breadcrumb>
    <Content>
      <div id="resourceTree">
        <div class="title">{{$t('Content.ID_RESOURCE')}}</div>
        <div class="controlTree">
          <!--<a><i class="el-icon-plus"></i></a>-->
          <a @click="getTree"><i class="el-icon-refresh"></i>{{$t('Content.ID_REFRESH')}}</a>
        </div>
        <el-tree :data="resourceTree" default-expand-all :expand-on-click-node="false" :highlight-current="true"
                 @node-click="handleNodeClick"></el-tree>
      </div>
      <div id="resourceList">
        <div class="title">{{treeName}}</div>
        <div class="controlBox">
          <div class="search">
            <div>
              <el-select v-model="value" :placeholder="$t('Content.ID_IMAGE_MODE')" @change="selectChange">
                <el-option v-for="item in select"
                           :key="item.value"
                           :label="item.label"
                           :value="item.value"></el-option>
              </el-select>
            </div>
            <div>
              <el-input :placeholder="$t('Msg.ID_MSG_5')" v-model="resName">
                <template slot="prepend">{{$t('Content.ID_RESOURCE_NAME')}}</template>
              </el-input>
            </div>
            <el-button @click="resourceQuery">{{$t('Content.ID_RESEARCH')}}</el-button>
          </div>
          <div class="control">
            <a @click="upload"><i class="el-icon-upload"></i>{{$t('Content.ID_UPLOAD')}}</a>
            <a @click="download"><i class="el-icon-download"></i>{{$t('Content.ID_DOWNLOAD')}}</a>
            <a @click="del"><i class="el-icon-delete"></i>{{$t('Content.ID_DELETE')}}</a>
          </div>
        </div>
        <div v-if="value == '2'" class="tableList">
          <el-table :data="resources" @selection-change="tableSelect">
            <el-table-column type="selection" align="center" width="55"></el-table-column>
            <el-table-column prop="name" align="center" :label="$t('Content.ID_NAME')"></el-table-column>
            <el-table-column prop="screenshot" align="center" :label="$t('Content.ID_THUMBNAIL')">
              <template slot-scope="scope">
                <img :src="baseURL + scope.row.thumbnail" width="100" height="70"/>
              </template>
            </el-table-column>
            <el-table-column prop="resolution" align="center" :label="$t('Content.ID_RESOLUTION')"></el-table-column>
            <el-table-column prop="size" align="center" :label="$t('Content.ID_SIZE')+'(kb)'"></el-table-column>
            <el-table-column prop="orgId" align="center" :label="$t('Content.ID_GROUP')"></el-table-column>
            <el-table-column prop="creator" align="center" :label="$t('Content.ID_CREATOR')"></el-table-column>
            <el-table-column prop="uploadtime" align="center" :label="$t('Content.ID_UPDATE_TIME')"></el-table-column>
          </el-table>
        </div>
        <div v-else class="imgList">
          <ul class="resourceList">
            <li v-for="(resource,id) in resources" :key="id" :id="resource.id"
                @click="selected($event)" :url="resource.url" @dblclick="preview($event)">
              <label class="el-upload-list__item-status-label">
                <i class="el-icon-upload-success el-icon-check"></i>
              </label>
              <div class="imgBox">
                <img :src="baseURL + resource.thumbnail">
              </div>
              <p>{{resource.name}}</p>
            </li>
          </ul>
        </div>
        <div class="page">
          <el-pagination
            @current-change="handleCurrentChange"
            :page-size="pageCount"
            layout="total, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
        <el-dialog
          :title="title + $t('Content.ID_UPLOAD')"
          :visible.sync="dialog"
          width="400px">
          <el-upload
            class="upload-demo"
            drag
            ref="upload"
            :data="data"
            :action=" baseURL + 'resource/upload'"
            :accept="accept"
            :headers="headers"
            :before-upload="beforeUp"
            :on-success="onSuccess"
            multiple>
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">{{$t('Msg.ID_MSG_6')}}<em>&nbsp;{{$t('Msg.ID_MSG_23')}}</em></div>
            <div class="el-upload__tip" slot="tip">{{$t('Content.ID_UPLOAD_DESCRIPTION')}}{{directions}}</div>
          </el-upload>
          <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="uploadCon">{{$t('Content.ID_OK')}}</el-button>
      </span>
        </el-dialog>  <!---->
        <el-dialog
          :title="title + $t('Content.ID_PREVIEW')"
          :visible.sync="view"
          width="30%"
          top="3vh"
          @close="viewClose"
        >
          <div style="width: 100%;height: 100%;text-align: center;overflow: hidden">
            <img v-if="format==='image'" style="width: 100%;height: 100%;" :src="url">
            <video preload="metadata" v-if="format ==='video'" id="myVideo" width="520" height="320" controls>
              <source :src="url" type="video/mp4">
            </video>
          </div>
        </el-dialog><!---->
      </div>
    </Content>
    <footer-bar></footer-bar>
  </div>
</template>
<script>
  import NavBar from '@/components/common/Navbar'
  import FooterBar from '@/components/common/footer'
  import Content from '@/components/common/content'
  import Breadcrumb from '@/components/common/Breadcrumb'
  import request from '@/utils/request'
  import {getTree} from "@/api/Tree";
  import {getResource, delResource} from "@/api/rsource";

  export default {
    mounted: function () {
      this.getTree();
      this.resourceQuery()
    },
    data() {
      return {
        baseURL: request.defaults.baseURL, //
        resourceTree: [],

        type: 'image',           //
        accept: 'image/png,image/jpg,image/jpeg',         //
        directions: this.$t('Msg.ID_MSG_7'),                      //
        dialog: false,
        title: '',
        value: '',
        treeId: 1,
        treeName: this.$t('Content.ID_IMAGE'),
        resources: [],
        row: [],
        url: '',                   //
        downloadUrl: '',          //
        downloadName: '',         //
        view: false,              //
        ids: [],
        select: [
          {
            value: 1,
            label: this.$t('Content.ID_IMAGE_MODE')
          },
          {
            value: 2,
            label: this.$t('Content.ID_LIST_MODE')
          }
        ],
        pageCount: 21,     //
        pageNo: 1,          //
        total: 0,            //
        format: '',               //
        resName: ''                //
      }
    },
    computed: {
      headers() {                                   //
        return {
          token: sessionStorage.getItem('token'),
          name: sessionStorage.getItem('name')
        }
      },
      data() {                                       //
        return {
          uploader: sessionStorage.getItem('userId'),
          groupId: this.treeId,
          type: this.type
        }
      }
    },
    components: {
      NavBar,
      FooterBar,
      Content,
      Breadcrumb
    },
    methods: {
      getTree() {
        let _this = this;
        let params = {id: 0};
        getTree(params).then(response => {
          _this.resourceTree = response.cust.trees
        })
      },                               //
      handleNodeClick(val) {
        this.treeId = val.id;
        this.type = val.treeType;
        this.treeName = val.label;
        let image = 'image/png,image/jpg,image/jpeg';
        let video = 'video/*';
        let audio = 'audio/*';
        if (image.indexOf(val.treeType) != -1) {
          this.accept = image;
          this.directions = this.$t('Msg.ID_MSG_7')
        } else if (video.indexOf(val.treeType) != -1) {
          this.accept = video;
          this.directions = this.$t('Msg.ID_MSG_8')
        } else if (audio.indexOf(val.treeType) != -1) {
          this.accept = audio;
          this.directions = ''
        } else {
          this.accept = '*';
          this.directions = ''
        }
        this.resourceQuery()

      },                    //

      upload() {
        this.title = this.treeName.slice(0, 2);
        this.dialog = true
      },                                //
      uploadCon() {
        this.$refs.upload.clearFiles();             //List
        this.dialog = false
      },                                  //
      beforeUp: function (file) {
        if (this.accept.indexOf(file.type.slice(0, 4)) == -1) {
          this.$message({
            message: this.$t('Msg.ID_MSG_25'),
            center: true,
            type: 'warning'
          });
          return false
        }
      },               //
      onSuccess: function (response) {
        if (response.code == '0000') {
          this.$message({
            message: this.$t('Content.ID_UPLOAD_SUCCESS'),
            center: true,
            type: 'success'
          });
          this.resourceQuery()
        } else {
          this.$message({
            message: response.data.infor + '',
            center: true,
            type: 'error'
          });
        }
      },          //
      del() {
        let ids = '';
        if (this.value == '2') {
          ids = this.row.map(item => item.id).join(' ');      //id
          if (ids == '') {
            this.$message({
              message: this.$t('Msg.ID_MSG_24'),
              center: true,
              type: 'warning'
            });
            return false
          }
        } else {
          ids = this.ids.join(' ');
          if (ids == '') {
            this.$message({
              message: this.$t('Msg.ID_MSG_24'),
              center: true,
              type: 'warning'
            });
            return false
          }
        }
        this.$confirm(this.$t('Msg.ID_MSG_9'), this.$t('Content.ID_PROMPT'), {
          confirmButtonText: this.$t('Content.ID_OK'),
          cancelButtonText: this.$t('Content.ID_CANCEL'),
          type: 'warning'
        }).then(() => {
          let params = {
            ids: ids
          };
          delResource(params).then(response => {
            this.resourceQuery();
            this.$message({
              message: this.$t('Content.ID_DELETE_SUCCESS'),
              showClose: true,
              center: true,
              type: 'success'
            })
          })
        })
      },                                   //
      preview(e) {
        this.url = this.baseURL + document.getElementById(e.currentTarget.id).getAttribute("url");
        this.title = this.treeName.slice(0, 2);
        if (this.url.indexOf('mp4') != '-1') {
          this.format = 'video';
        } else if (this.url.indexOf('png') != '-1' || this.url.indexOf('jpg') != '-1' || this.url.indexOf('jpeg') != '-1') {
          this.format = 'image'
        } else {
          this.$message({
            message: this.$t('Msg.ID_MSG_26'),
            center: true,
            type: 'warning'
          });
          return false
        }
        this.view = true;
        if (this.format == 'video') {
          let myVideo = document.getElementById('myVideo');
          myVideo.load();
        }
      },                              //
      selected(e) {
        let dom = e.currentTarget.id;
        let target = e.currentTarget;
        /*if (e.target.tagName == 'DIV' || e.target.tagName == 'IMG' || e.target.tagName == 'P') {          //LILI
          dom = e.target.offsetParent.id
          target = e.target.offsetParent
        } else {
          dom = e.target
          target = e.target
        }*/     //currentTargettarget
        let children = target.children[0];
        if (children.style.display != 'block') {
          children.style.display = 'block';
          target.style.backgroundColor = "#ebebeb";

          this.downloadUrl = this.baseURL + document.getElementById(target.id).getAttribute("url")
          this.downloadName = target.children[2].innerText;
          this.ids.push(dom)
        } else {
          children.style.display = 'none';
          target.style.backgroundColor = "white";
          this.downloadUrl = '';
          this.downloadName = '';
          for (let i = 0; i < this.ids.length; i++) {
            if (this.ids[i] == dom) this.ids.splice(i, 1)
          }    //ids
        }
      },                             //
      resourceQuery() {
        let _this = this;
        _this.resources = [];
        let params = {
          groupId: this.treeId,
          pageCount: this.pageCount,
          pageNo: this.pageNo,
          name: this.resName
        };
        getResource(params).then(response => {
          let resources = response.cust.resources;
          _this.total = response.cust.pages.count;
          for (let resource of resources) {
            _this.resources.push(resource)
          }
        })
      },                         //
      selectChange(val) {
        val == '2' ? this.pageCount = 5 : this.pageCount = 21;
        this.resourceQuery()
      },                       //
      download() {
        if (this.value == '2') {
          this.downloadUrl = this.baseURL + this.row.map(item => item.url).join('');
          this.downloadName = 'http://' + this.row.map(item => item.name).join('')
        }
        if (this.downloadUrl != '' || this.downloadName != '') {
          let a = document.createElement('a');
          a.href = this.downloadUrl;
          a.download = '';
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
        } else {
          this.$message({
            message: this.$t('Msg.ID_MSG_24'),
            showClose: true,
            center: true,
            type: 'warning'
          })
        }
      },                              //
      handleCurrentChange(val) {
        this.pageNo = val;
        this.resourceQuery()
      },                //
      tableSelect(row) {
        this.row = row
      },                        //
      viewClose() {
        let myVideo = document.getElementById('myVideo');
        myVideo.currentTime = 0;
        myVideo.pause();
      },
      langChange() {
        this.getTree();
        this.select = [
          {
            value: 1,
            label: this.$t('Content.ID_IMAGE_MODE')
          },
          {
            value: 2,
            label: this.$t('Content.ID_LIST_MODE')
          }
        ];
        this.treeName = this.$t('Content.ID_IMAGE');
        this.directions = this.$t('Msg.ID_MSG_7');
      }
    }
  }
</script>





